header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  .site-menu {
    border-top: 1px solid $color-white;
    border-bottom: 1px solid $color-white;
  }
  li {
    position: relative;
  }
  // ---------------------------
  // li.top-level-link Start.
  // ---------------------------
  li.top-level-link {
    height: 110px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  li.top-level-link > a {
    padding: 8px 50px;
    display: inline-block;
    position: relative;
    text-decoration: none;
    color: $color-white;
    font-family: $font-heading;
    font-size: 16px;
    text-transform: uppercase;
    transition: all 0.3s ease-in-out;
    .menu-item-text {
      position: relative;
      z-index: 1;
    }
  }
  li.top-level-link:hover > a,
  li.top-level-link.current-menu-item a {
    color: $color-olive-green;
    transition: all 0.3s ease-in-out;
    .menu-item-active-hover {
      clip-path: inset(0 0 0 0);
      transition: all 0.3s ease-in-out;
    }
  }
  // ---------------------------
  // li.top-level-link End.
  // ---------------------------
  // Submenu design. Start.
  .sub-menu {
    transition: max-height 0.3s ease-in-out;
    top: 100%;
    max-height: 0;
    overflow: hidden;
    position: absolute;
    background: #fff;
    width: 100%;
    background-color: $color-white;
    a {
      display: block;
      padding: 10px 20px;
      text-decoration: none;
      text-transform: none;
      font-size: 18px;
      color: $color-gray-2;
      font-family: $font-cursive;
      .menu-item-text {
        display: inline-block;
        &::after {
          content: '';
          display: block;
          width: 0%;
          height: 1px;
          background-color: $color-olive-green;
          transition: all 0.3s ease-in-out;
        }
      }
      &:hover {
        color: $color-olive-green;
        .menu-item-text::after {
          width: 100%;
          transition: all 0.3s ease-in-out;
        }
      }
    }
    .menu-item-active-hover {
      display: none;
    }
    .sub-menu {
      position: relative;
      padding-left: 15px;
    }
    li.menu-item-has-children > a::after {
      content: '';
      position: absolute;
      display: inline-block;
      right: 10px;
      top: 11px;
      background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg id='SVGRepo_bgCarrier' stroke-width='0'%3E%3C/g%3E%3Cg id='SVGRepo_tracerCarrier' stroke-linecap='round' stroke-linejoin='round'%3E%3C/g%3E%3Cg id='SVGRepo_iconCarrier'%3E%3Cpath d='M5.70711 9.71069C5.31658 10.1012 5.31658 10.7344 5.70711 11.1249L10.5993 16.0123C11.3805 16.7927 12.6463 16.7924 13.4271 16.0117L18.3174 11.1213C18.708 10.7308 18.708 10.0976 18.3174 9.70708C17.9269 9.31655 17.2937 9.31655 16.9032 9.70708L12.7176 13.8927C12.3271 14.2833 11.6939 14.2832 11.3034 13.8927L7.12132 9.71069C6.7308 9.32016 6.09763 9.32016 5.70711 9.71069Z' fill='%23878787'%3E%3C/path%3E%3C/g%3E%3C/svg%3E");
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      width: 20px;
      height: 20px;
      transform: rotate(-90deg);
      transition: transform 0.3s ease-in-out;
    }
    li.menu-item-has-children:hover > a::after {
      transform: rotate(0deg);
      transition: transform 0.3s ease-in-out;
    }
  }
  li:hover > .sub-menu {
    max-height: 600px;
  }
  // Submenu design. End.
  // -----------------------

  a.custom-logo-link {
    padding: 0 30px;
    img.custom-logo {
      width: 100%;
      max-width: 138px;
      height: auto;
    }
  }

  .header-search {
    text-align: center;
    margin-top: 30px;
    form {
      display: inline-block;
    }
    .search-field {
      background: transparent;
      border: none;
      color: $color-white;
      font-family: $font-heading;
      caret-color: $color-white;
      width: 240px;
      border-bottom: 1px solid #ffffff;
      padding: 10px;
      &:focus-visible {
        outline: none;
      }
      &::-webkit-search-cancel-button {
        -webkit-appearance: none;
        height: 14px;
        width: 14px;
        background-image: url("data:image/svg+xml;utf8,\
    <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='%23ffffff' stroke-width='2'>\
    <line x1='18' y1='6' x2='6' y2='18'/>\
    <line x1='6' y1='6' x2='18' y2='18'/>\
    </svg>");
        background-size: contain;
        cursor: pointer;
      }
    }
    .search-submit {
      background: transparent;
      border: none;
    }
    img {
      width: 20px;
      height: 20px;
    }
    /* Modern */
    ::placeholder {
      color: $color-white;
      font-family: $font-heading;
      font-size: 20px;
      opacity: 1;
    }

    /* Chrome, Safari */
    ::-webkit-input-placeholder {
      color: $color-white;
      opacity: 1;
    }

    /* Firefox */
    ::-moz-placeholder {
      color: $color-white;
      opacity: 1;
    }

    /* Edge (old) */
    :-ms-input-placeholder {
      color: #888;
    }
  }
}
